
import Search from 'vux/src/components/search/index.vue'

import searchTab from './searchTab'
import schoolList from './schoolList'
import { searchSchool } from '@/api/index.js'
export default {
components: { Search, searchTab, schoolList },
data () {
return {
curTab: 'all',
end: false,
loading: false,
key: '',
placeholder: '搜索',
list: [],
query: {
size: 0,
schoolname: ''
}
}
},
computed: {
openid () {
return this.$store.state.user.openid
},
toUrl () {
return this.$store.state.user.returnUrl
}
},
mounted () {
this.$nextTick(() => {
this.$refs.search.isFixed = true
this.$refs.search.isFocus = true
this.$refs.search.isCancel = false
this.$refs.search.setFocus()
this.$refs.list.init()
})
},
methods: {
returnBack () {
this.list = []
this.key = ''
if (this.toUrl !== '') {
this.$router.push({ path: this.toUrl })
} else {
this.$router.go(-1)
}
},

nextPage () {
this.page++
this.getList()
},

reList () {
this.page = 0
this.list = []
this.loading = true
this.getList()
this.$nextTick(() => {
this.$refs.list.schoolScroll && this.$refs.list.schoolScroll.refresh()
})
},

searchKey (key) {
this.page = 0
this.query = {
schoolname: key,
openid: this.openid,
page: this.page
}
this.list = []
console.log(key)
if (key !== undefined && key !== '') {
this.loading = true
this.getList()
}
},
getList () {
this.query.page = this.page
if (this.page === 0) {
this.end = false
}
searchSchool(this.query).then(res => {
this.loading = false
if (res.data.code === 0) {
if (res.data && res.data.data && res.data.data.length < 20) {
this.end = true
}
this.list = this.list.concat(res.data.data)
this.$nextTick(() => {
this.$refs.list.schoolScroll && this.$refs.list.schoolScroll.refresh()
})
}
})
}
}
}



